import React, { Component } from 'react';
import s from './Card.module.scss';
import cl from "classnames";
import {CheckSquareOutlined, DeleteOutlined} from "@ant-design/icons";
class Card extends React.Component {

    state = {
        done: false,
    };

    handleCardClick = () => {
        this.setState((state) => {
            return {
            done: state.isRemembered ? true : !state.done,
            }
        });
    }

    handleIsRememberClick = () => {
        this.setState((state) => {
            return {
                isRemembered: true,
            }
        })
    }

    handleDeletedClick = () => {
        console.log("#####: 1 level");
        this.props.onDeleted();
    }

    render() {
        const {eng, rus} = this.props;
        const {done, isRemembered} = this.state;
        const cardClass = [s.card];
        return (
            <div className={s.root}>
                <div
                    className={cl(s.card, {[s.done]: done})}
                    onClick={this.handleCardClick}
                >
                    <div className={cl(s.cardInner,{[s.isRemembered]: isRemembered})}>
                        <div className={s.cardFront}>
                            { eng }
                        </div>
                        <div className={s.cardBack}>
                            { rus }
                        </div>
                    </div>
                </div>
                <div className={s.icons} onClick={this.handleIsRememberClick}>
                    <CheckSquareOutlined/>
                </div>
                <div className={s.icons} onClick={this.handleDeletedClick}>
                    <DeleteOutlined />
                </div>
            </div>
        );
    }

}

export default Card;